.section
{
    position: relative;

    &__number
    {
        font-size: 12px;
        font-weight: 400;

        position: absolute;
        top: 0;
        right: 0;

        display: flex;

        margin: 0;

        color: #e5e5e5;

        @media(min-width: 768px)
        {
            font-size: 20px;
        }

        &--big
        {
            font-size: 18px;
            font-weight: 700;

            margin-top: 0;
            margin-right: 5px;
            margin-bottom: 0;

            @media(min-width: 768px)
            {
                font-size: 36px;
            }
        }
    }

    &__title
    {
        font-size: 20px;
        font-weight: 700;
        line-height: 30px;

        margin-top: 90px;
        margin-bottom: 40px;
    }

    &__subtitle
    {
        margin-top: 0;
        margin-bottom: 20px;

        color: #49b462;

        @include MobileHeadline2;

        @media(min-width: 768px)
        {
            @include TabletHeadline2;
        }

        @media(min-width: 1024px)
        {
            @include DesktopHeadline2;
        }
    }

    &__description
    {
        @include MobileBody1Regular;

        @media(min-width: 768px)
        {
            @include TabletBody1Regular;
        }

        @media(min-width: 1024px)
        {
            @include DesktopBody1Regular;
        }
    }
}
