.versus
{
    &__subtitle
    {
        margin-top: 50px;
        margin-bottom: 32px;
    }

    &__list
    {
        display: grid;

        margin-top: 0;
        margin-bottom: 40px;
        padding-left: 0;

        list-style: none;

        gap: 30px;
        grid-template-columns: 100%;

        @media(min-width: 768px)
        {
            margin-bottom: 75px;

            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: 1fr 1fr;
        }
    }

    &__item
    {
        padding-top: 28px;
        padding-bottom: 28px;
        padding-left: 20px;

        border-radius: 10px;
        background: #fff;

        filter: drop-shadow(0px 0px 14px rgba(0, 0, 0, .1));

        @media(min-width: 768px)
        {
            max-width: 380px;
            max-height: 136px;
        }
    }

    &__text
    {
        margin: 0;

        @include DesktopBody2Regular;
    }
}
