.modal
{
    z-index: 20;

    &-map
    {
        &__overlay
        {
            display: none;

            &--active
            {
                position: fixed;
                z-index: 20;
                top: 0;
                left: 0;

                display: block;

                width: 100%;
                height: 100%;

                content: '';

                background: rgba(0, 0, 0, .6);
            }
        }

        &__wrapper
        {
            position: fixed;
            z-index: 21;
            top: calc(50% - 312.5px);
            left: calc(50% - 192px);

            display: none;

            max-width: 100%;
            padding: 30px;

            border-radius: 10px;
            background: #fff;

            &--active
            {
                display: block;
            }

            @media(min-width: 768px)
            {
                top: calc(50% - 312.5px);
                left: calc(50% - 344.5px);
            }

            @media(min-width: 1024px)
            {
                top: calc(50% - 312.5px);
                left: calc(50% - 407px);
            }
        }

        &__close
        {
            position: absolute;
            z-index: 22;
            top: 5px;
            right: 0;

            margin-left: calc(100% - 23px);

            border: none;
            background: transparent;
        }

        &__map
        {
            z-index: 21;

            padding: 40px;

            border: none;
            border-radius: 5px;
        }

        &__frame
        {
            z-index: 21;

            width: 320px;
            height: 500px;

            border: none;
            border-radius: 10px;

            @media(min-width: 768px)
            {
                width: 625px;
                height: 500px;
            }

            @media(min-width: 1024px)
            {
                width: 750px;
                height: 500px;
            }
        }
    }
}
