.footer {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-top: 90px;
	margin-bottom: 30px;

	@media(min-width: 1024px) {
		flex-direction: row;
	}

	&__logo {
		width: 157px;
		height: 36px;
		margin-left: calc(50% - 78.5px);

		@media(min-width: 1024px) {
			margin: 0;
		}
	}

	&__nav {
		@media(min-width: 1024px) {
			width: 460px;
		}
	}

	&__button {
		align-items: center;
		padding: 0;

		font-weight: 700;
		font-size: 14px;
		text-align: center;
		color: #49b462;
		text-transform: uppercase;

		background: transparent;
		border: none;
	}

	&__geo {
		width: 24px;
		height: 24px;
		margin-right: 5px;
		margin-bottom: -5px;
	}
}
