.advantage {
	&__title {
		margin-top: 0;
		margin-bottom: 18px;

		font-weight: 700;
		font-size: 16px;

		@media(min-width: 768px) {
			font-size: 18px;
		}
	}

	&__text {
		max-width: 215px;
		margin: 0;

		font-weight: 400;
		font-size: 14px;

		@media(min-width: 650px) {
			max-width: 100%;
		}

		@media(min-width: 768px) {
			font-size: 16px;
		}
	}
}
